* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
html {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}
h1 {
  margin: 0;
}
body {
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  height: 100%;
  font-size: 16px;
  position: relative;
  font-family: 'Montserrat', sans-serif;
  transition: 0.3s;
  display: flex;
  background-color: @fake-white-color;
  flex-direction: column;
  justify-content: space-between;
  // padding-right: 20px;
  @media(min-width: @screen-lg) and (-webkit-min-device-pixel-ratio: 1.2) {
    font-size: 14px;
  }
}
body::-webkit-scrollbar { width: 0px; height: 0px;}
body::-webkit-scrollbar-button { display: none; background-color: transparent; }
body::-webkit-scrollbar-track {  background-color: transparent;}
body::-webkit-scrollbar-track-piece { background-color: transparent;}
body::-webkit-scrollbar-thumb { height: 70px; background-color: transparent; border-radius: 0px;}

.container {
  padding: 0 10px;
  margin: 0 auto;
  width: @screen-lg;
  box-sizing: border-box;
  @media(min-width: @screen-xs) {
    width: 90%;
    min-width: @screen-xs;
  }
  @media(max-width: @screen-lg) {
    max-width: @screen-lg;
  }
}
