// Для импорта в диспетчер подключений: @import './src/less/blocks/footer-list.less';


.footer-list {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: row;
  align-items: baseline;

  &__item {
    list-style-type: none;

    p {
      .text(@black-color, 0.8em);
      margin-right: 15px;
    }
    a {
      .text(@black-color, 0.8em);
      margin-right: 15px;
    }
  }
}
