// Для импорта в диспетчер подключений: @import './src/less/blocks/page-footer.less';


.page-footer {
  margin-top: auto;
  padding-bottom: 30px;
  // background-color: @fake-white-color;
  z-index: 100;
  background: linear-gradient(to bottom, rgba(245, 245, 245, 0) 0%, #f5f5f5 40%);
  // height: 10;

  &__inner {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: space-between;
  }

  &__text {
    .text(@black-color, 0.8em);
  }
}
