// Для импорта в диспетчер подключений: @import './src/less/blocks/sidebar.less';


.sidebar {
  flex-basis: 23%;
  display: -ms-flex;
  width: 23%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-align: start;
  align-items: flex-start;
  position: fixed;
  padding-left: 35px;
  overflow: visible;
  height: 80vh;
  z-index: 10;
  right: 0;

  &:after {
    content: '';
    display: inline-block;
    height: 200%;
    width: 200%;
    position: absolute;
    top: 50%;
    right: -900%;
    transform: translate(-50%, -50%);
    background: linear-gradient(to right, rgba(245, 245, 245, 0) 0%, #f5f5f5 70%);
    z-index: 0;
  }

  &:before {
    content: '';
    display: inline-block;
    height: 120vh;
    width: 2px;
    background-color: @light-gray-color;
    position: absolute;
    left: 0;
  }

  &__list {
    position: absolute;
    z-index: 10;
  }
  &__item {
    list-style-type: none;
    position: relative;
    z-index: 10;

    &:hover .submenu1st {
      display: block;
    }
  }
  .submenu1st .submenu__item:hover .submenu2nd {
    display: block;
  }
  &__link {
    .text(@black-color, 0.8em);
    // white-space: nowra p;
    line-height: 26px;

    &:hover {
      color: @blue-color;
      text-decoration: underline;
    }
  }

}
.sidebar::-webkit-scrollbar { width: 0px; height: 0px;}
.sidebar::-webkit-scrollbar-button { display: none; background-color: transparent; }
.sidebar::-webkit-scrollbar-track {  background-color: transparent;}
.sidebar::-webkit-scrollbar-track-piece { background-color: transparent;}
.sidebar::-webkit-scrollbar-thumb { height: 70px; background-color: transparent; border-radius: 0px;}
