// Для импорта в диспетчер подключений: @import './src/less/blocks/scroll.less';


.scroll {
  display: inline-block;
  width: 40%;
  height: 4px;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: row;
  border-radius: 3px;

  &__item {
    width: 20%;
    height: 100%;
    background-color: @gray-color;
    transition: all 700ms cubic-bezier(0.65, 0.08, 0.37, 0.99);
  }
  .active {
    background-color: @black-color;
    transition: all 700ms cubic-bezier(0.65, 0.08, 0.37, 0.99);
  }
}
