// Для импорта в диспетчер подключений: @import './src/less/blocks/submenu.less';


.submenu {
  padding-left: 15px;
  // height: 0;
  // opacity: 0;
  transition: 0.3s;
  display: none;
  background-color: @fake-white-color;
  position: absolute;
  top: 50%;
  right: 100%;
  width: 100%;
  transform: translateY(-50%);
  padding: 10px;
  z-index: 10;


  &__item {
    list-style-type: none;
    position: relative;
  }
  &__link {
    .text(@black-color, 0.8em);
    line-height: 1.9;
    &:hover {
      color: @blue-color;
      text-decoration: underline;
    }
  }
}
