// Для импорта в диспетчер подключений: @import './src/less/blocks/city.less';


.city {
text-align: right;
  flex-basis: 64%;
  padding-right: 120px;

  &__wrapper {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
  }

  &__name {
    .text(@blue-color, 0.8em);
    margin-left: 20px;
    text-transform: uppercase;
    &:first-of-type {
      margin-left: 100px;
    }
  }

  &__btn {
    .btn(120px, @black-color, @gray-color, @blue-color);
    height: 40px;
    line-height: 39px;
    font-size: 1em;
    text-transform: uppercase;
    text-align: left;
    padding-left: 20px;
    font-weight: 500;
    &:last-of-type {
      background-color: @gray-color;
    }
  }

  &__select {
    background-color: transparent;
    border: 0;
    .text(@black-color, 0.8em);
  }
}
